@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+HK:wght@100;300;400;500;700;900&display=swap);
body{margin:0;padding:0;box-sizing:border-box;font-family:'Noto Sans HK', sans-serif}body #root{width:100vw;min-height:100vh;background-color:#fffffe;margin:0;padding:0}body #root .body{margin:0 8em;padding:2em}body #root .body .filters{display:flex;flex-direction:row;justify-content:space-between;margin-bottom:1em}body #root .body .filters>div{width:33%;display:flex;flex-direction:row;align-items:center}body #root .body .filters>div input[type='checkbox']{margin-left:3em !important}body #root .body .filters>div p{margin-right:1em}body #root .body .filters .loading svg{color:#ef4565}body #root .body .filters .loaded svg{color:#3da9fc}h1,h2,h3,h4,h5,h6{color:#094067;margin-bottom:1em}p,label{color:#5f6c7b}a{color:#ef4565}input[type='checkbox']{width:12px !important;height:12px !important;-webkit-appearance:none;-moz-appearance:none;-o-appearance:none;appearance:none;outline:2px solid #3da9fc;outline-offset:2px;box-shadow:none;margin:0}input[type="checkbox"]:checked{background-color:#3da9fc}input[type="checkbox"]:disabled{outline-color:lightgrey}input[type="checkbox"]:disabled:checked{background-color:lightgrey}.card{border:2px solid #094067;border-radius:5px;padding:0.75rem 1.75rem;margin-bottom:1.5em}.card .card-header{display:flex;flex-direction:row;justify-content:space-between;border-bottom:1px dotted #094067}.card .card-body{margin:1em 0;display:flex;flex-direction:row;justify-content:space-between}.card .card-body a svg{margin-left:1rem}.card .card-body .read-saved{display:flex;align-items:flex-end;flex-direction:column}.card .card-body .read-saved input[type="checkbox"]{margin-left:2em !important}.card.read{background:lightgray}.card.read a{color:#5f6c7b}.card.read input[type="checkbox"]{outline-color:grey}.card.read input[type="checkbox"]:checked{background-color:grey}

